import { defineComponent, reactive, ref, useContext } from '@nuxtjs/composition-api';
export default defineComponent({
    props: {
        showRelationship: {
            type: Boolean,
            default: false
        },
        dobPicker: {
            type: String,
            default: 'date'
        },
        btnSubmit: {
            type: Object,
            default: () => ({
                text: 'next'
            })
        },
        title: {
            type: String,
            default: 'register information'
        },
        backgroundColor: {
            type: String,
            default: 'transparent'
        }
    },
    setup(props, { emit }) {
        // DECLARE VARIABLE
        const { i18n } = useContext();
        const userProfileForm = ref(null);
        const validForm = ref(false);
        const formData = reactive({
            name: null,
            dob: null,
            gender: null
        });
        const requiredRule = (v) => !!v || i18n.t('required field');
        const nameRule = (v) => (v === null || v === void 0 ? void 0 : v.indexOf(' ')) > 1 || i18n.t('please enter fullname');
        const genderOptions = ref([
            {
                text: 'male',
                value: 'M'
            },
            { text: 'female', value: 'F' }
        ]);
        const relationship = ref(null);
        const relationships = ref([
            {
                text: i18n.t('child').toLowerCase(),
                value: 'child'
            },
            {
                text: i18n.t('parent').toLowerCase(),
                value: 'parent'
            },
            {
                text: i18n.t('sibling').toLowerCase(),
                value: 'sibling'
            },
            {
                text: i18n.t('spouse').toString(),
                value: 'spouse'
            }
        ]);
        // LOGIC
        const getDobLabel = () => {
            const timeUnit = props.dobPicker === 'date' ? 'date' : 'year';
            return `${timeUnit} of birth`;
        };
        const submit = () => {
            userProfileForm.value.validate();
            for (const key in formData) {
                if (!formData[key])
                    validForm.value = false;
            }
            if (!relationship.value && props.showRelationship)
                validForm.value = false;
            if (!validForm.value)
                return;
            if (props.showRelationship)
                formData.relationship = relationship.value;
            emit('user-profile-data', formData);
            console.log(validForm.value, 'line 147');
        };
        return {
            formData,
            userProfileForm,
            validForm,
            genderOptions,
            relationships,
            relationship,
            requiredRule,
            getDobLabel,
            nameRule,
            submit
        };
    }
});
