import { defineComponent, onMounted, ref, watch } from '@nuxtjs/composition-api';
import moment from 'moment';
export default defineComponent({
    props: {
        wAttr: {
            type: Object,
            default: () => ({})
        },
        defaultValue: {
            type: Number || String,
            default: null
        }
    },
    setup(props, { emit }) {
        const ranges = ref([]);
        const yearField = ref(Number(props.defaultValue));
        onMounted(() => {
            for (let year = moment().year(); year >= 1900; year--)
                ranges.value.push(year);
        });
        watch(yearField, () => {
            if (yearField.value)
                emit('data', yearField.value);
        });
        return {
            ranges,
            yearField
        };
    }
});
